import React, { FC, useEffect, useRef } from 'react';
import { DOMElement, PolygonProps } from './Polygon.types';
import { draw } from '../../svg/svg.methods';
import { divide, ngon, rad } from '../../geometry';
import { FaBeer } from 'react-icons/fa';

export const Polygon: FC<PolygonProps> = ({
	sides = 6,
	rotation = 0,
	scale = 1,
	cornerRadius = 0,
	fillColor = '#000',
	strokeColor = '#000',
	lineWidth = 0.1,
	children = <FaBeer/>
}) => {
	const [points, viewBox] = ngon(sides, scale, rad(rotation), cornerRadius, [0, 0]);
	const scaledLineWidth = lineWidth * scale;
	const hl = scaledLineWidth > 0 ? scaledLineWidth/2:0;
	const newViewBox = [viewBox[0]-hl, viewBox[1]-hl, viewBox[2]+scaledLineWidth, viewBox[3]+scaledLineWidth];
	const ref = useRef(null);
	useEffect(() => {
		const knownPath = ref.current! as SVGPathElement;
		if(!children) return;
		const nextChild = knownPath.nextSibling as DOMElement;
		console.log(nextChild);
		if(nextChild.tagName === 'svg'){
			const childViewBox = nextChild.getAttribute('viewBox')?.split(' ').map(v=>parseFloat(v))!;
			const maxDim = Math.max(childViewBox[2], childViewBox[3]);
			const downScale = scale*2 / maxDim;
			const childRadius = divide(childViewBox, 2);
			const svgChildren = Array.from(nextChild.children);

			//nextChild.setAttribute('transform', `scale(${downScale})`);
			nextChild.setAttribute('viewBox', `0 0 ${downScale * childViewBox[2]} ${downScale * childViewBox[3]}`);
			nextChild.removeAttribute('width');
			nextChild.removeAttribute('height');
			nextChild.setAttribute('x', `-${scale}`);
			nextChild.setAttribute('y', `-${scale}`);
			svgChildren.forEach((el, i) => {
				el.setAttribute('transform', `scale(${downScale})`);
			})
			console.log("do something with this", childViewBox, maxDim, childRadius);
		}
	},[scale])
	return (<svg xmlns="http://www.w3.org/2000/svg" viewBox={newViewBox.join(' ')} width="100px" height="100px">
		<path d={draw(points)} ref={ref} fill={fillColor} stroke={strokeColor} strokeWidth={scaledLineWidth}/>
		{children}
	</svg>)
};